import { CircularProgress, Grid, Typography, Box, useTheme } from "@mui/material";
import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import { dataUtil, usage_type } from "../../wsclient/crmservices/dataUtil";
import { getLogoImage } from "../../utils/common";
import { useTranslation } from "react-i18next";
import { makeStyles } from "@mui/styles";
import AspectRatioBox from "../../components/AspectRatioBox";

export default function PromotionsView(props){
    const { t } = useTranslation();
    const { showLoadingPromotions, promotions } = props;

    return (
        <Grid item xs={12} my={4}>
            <Typography variant='h4'>{t('order_promotion')}</Typography>
            {showLoadingPromotions && <center><CircularProgress/></center>}
            <Grid item xs={12} my={2}>
                <Swiper
                    spaceBetween={10}
                    slidesPerView={3}
                    onSlideChange={() => {}}
                    onSwiper={(swiper) => {}} 
                    breakpoints={{
                        320: {
                            slidesPerView: 1
                        },
                        768: {
                            slidesPerView: 2, 
                        },
                        1024: {
                            slidesPerView: 3, 
                        }
                    }}
                >
                    {promotions && promotions.content && promotions.content.map((item, index) => {
                        return (
                            <SwiperSlide key={index}>
                                <PromotionItem item={item} />
                            </SwiperSlide>
                        )
                    })}
                </Swiper>
            </Grid>
        </Grid>
    )
}

function PromotionItem(props){
    const { item } = props;
    const theme = useTheme();
    const classes = useStyles(theme);
    let imageSource = dataUtil.getImageByUsageType(item.creatives, usage_type.offer_detail);
    return (
        <Box width={'100%'} borderColor={theme.palette.border.main} position='relative'>
            {imageSource && imageSource.length > 0 ? <AspectRatioBox ratio={16/9}><img src={imageSource[0].uri} className={classes.image} /></AspectRatioBox>
                : <div className={classes.image} />}
            <Typography marginTop={1} variant="body1" textOverflow={'ellipsis'}>{item.name}</Typography>            
            {item.short_description && <Grid item xs={12}>
                <Typography variant="subtitle2">{item.short_description}</Typography>
            </Grid>}
        </Box>
    )
}

const useStyles = makeStyles({
    image: (props) => ({
        height: '100%',
        objectFit: 'cover',
        width: '100%',
        borderRadius: props.spacing(2),
        borderColor: props.palette['grey']['300'],
        borderWidth: 1,
        borderStyle: 'solid',
    }),
})